import React from 'react'
import './Navbar.css';
import { NavLink } from 'react-router-dom';
function Navbar() {
    return (
        <>
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container-fluid">
          <NavLink className="navbar-brand" to="/">The Technicals</NavLink>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink activeClassName='menu_active' exact className="nav-link active" aria-current="page" to="/">Home</NavLink>
              </li>
              
              
              <li className="nav-item">
                <NavLink activeClassName='menu_active' className="nav-link" to="/services">Service</NavLink>
              </li>
              

              <li className="nav-item">
                <NavLink activeClassName='menu_active' className="nav-link" to="/about">About</NavLink>
              </li>
              

              <li className="nav-item">
                <NavLink activeClassName='menu_active' className="nav-link" to="/contact">Contact</NavLink>
              </li>
              
            </ul>
          </div>
        </div>
      </nav>
        </>
    )
}

export default Navbar
